EA+EY,kBAAA;EACA,SAAA;EACA,YAAA;EACA,2BAAA;A5B08CZ;A4Bv8CQ;EACI,QAAA;EACA,0BAAA;A5By8CZ;A4Bt8CQ;EACI,SAAA;EACA,2CAAA;A5Bw8CZ;A4Br8CQ;EACI,SAAA;EACA,0BAAA;A5Bu8CZ;AAhiDA;EACI,SAAA;EACA,UAAA;EACA,kBAAA;EACA,kBAAA;EACA,6BAAA;AAkiDJ","sourcesContent":["\nbody {\n    font-family: 'Roboto', sans-serif !important;\n    font-weight: 300;\n}\n  \n@keyframes spinner {\n    to {\n        transform: translate(-50%, -50%) rotate(360deg);\n    }\n}\n\n.spinner {\n    >*{\n        opacity: 0.08 !important;\n    }\n    &:before {\n        content: '';\n        transform: translate(-50%, -50%) ;\n        position: absolute;\n        z-index: 2000;\n        top: 50%;\n        left: 50%;\n        width: 30px;\n        height: 30px;\n        border-radius: 50%;\n        border-width:2px;\n        border-style: solid;\n        animation: spinner .6s linear infinite;\n    }\n}\n\n\n.tooltip{\n    z-index: 1000000;\n}\n  ","@import \"../../common/less/index\";\n\n@import \"variables\";\n@import \"hero\";\n@import \"intro\";\n@import \"slidedeck\";\n\n\nbody {\n    margin: 0;\n    padding: 0;\n    overflow-x: hidden;\n    overflow-y: scroll;\n    scroll-snap-type: y mandatory;\n}\n\n",".userinfo_toggler{\n\n  .userContainer {\n    text-align: center;\n    img {\n      width: 90px;\n    }\n  }\n}\n",".appbar {\n    height:@appbarHeight;\n    position: relative;\n    border: none !important;\n    display: flex;\n    align-items: center;\n    gap: 10px;\n    padding-left:10px;\n    padding-right:10px;\n\n    .icon {\n        height: @toolbarHeight - 20;\n    }\n    .title {\n        h1 {\n            font-size: 24px;\n            font-weight: 200;\n            letter-spacing: 6px;\n            margin: 0;\n        }\n        h2 {\n            font-size: 14px;\n            font-weight: 400;\n            letter-spacing:4px;\n            margin: 0;\n        }\n    }\n\n    .spacer {\n        flex-grow: 1;\n    }\n\n    .group {\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n    }    \n}\n",".list {\n    padding: var(--menu-container-padding);\n    margin: 0;\n    border: 0;\n\n    .list-item {\n      font-weight: 400;\n      height: @buttonHeight;\n      font-size: var(--label-font-size);\n      padding: var(--menu-container-padding);\n      border-radius: @borderRadiusSmall;\n\n      &:hover{\n        background-color: rgb(var(--list-item-hover-bg-color));\n        .list-item-action {\n          opacity: 1; \n          transform: scale(1); \n          transition: all 0.2s ease;\n        }\n      }\n      &.selected {\n        background-color: rgb(var(--list-item-selected-bg-color));\n        .list-item-action {\n          opacity: 1; \n          transform: scale(1); \n          transition: all 0.2s ease;\n        }\n      }\n\n      .list-item-action {\n        opacity: 0; \n        transform: scale(0.5); \n        transition: all 0.2s ease;\n        font-size: 20px;\n        min-width: 25px;\n      }\n\n      &:before {\n        background-color: rgb(var(--tint-color));\n        block-size: 16px;\n        border-radius: 3px;\n        content: \"\";\n        inline-size: 3px;\n        inset-inline-start: 0;\n        opacity: 0;\n        position: absolute;\n        transform: scaleY(0);\n        transition: transform 170ms cubic-bezier(0, 0, 0, 1);\n        margin-left:2px;\n      }\n\n      &.selected {\n        &:before {\n          opacity: 1;\n          transform: scaleY(1);\n        }\n      }\n    }\n  }",".toolbar {\n  position: relative;\n  display: flex;\n  align-items: center;\n  gap: @buttonSpacing;\n  padding-right: 10px;\n  height: @toolbarHeight;\n  padding: var(--menu-container-padding);\n\n  * {\n    outline: none;\n  }\n\n  .spacer {\n    flex-grow: 1;\n  }\n\n  .group {\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    height:100%;\n\n    .statusIndicator {\n      margin: 20px;\n\n      img {\n        display: block;\n        cursor: pointer;\n        margin: auto;\n      }\n\n      span {\n        font-size: .8em;\n        text-align: center;\n        width: 100%;\n        display: inline-block;\n        white-space: nowrap;\n      }\n\n      .notSupported {\n        display: none;\n        font-weight: bold;\n      }\n\n      .connected {\n        display: block;\n      }\n\n      .disconnected {\n        display: none;\n      }\n    }\n\n    .statusIndicator.disabled {\n      img {\n        cursor: not-allowed;\n\n        &:hover {\n          box-shadow: none;\n        }\n      }\n\n      .notSupported {\n        display: block;\n      }\n\n      .connected {\n        display: none;\n      }\n\n      .disconnected {\n        display: none;\n      }\n    }\n\n    .statusIndicator.error:not(.disabled) {\n      span {\n        font-weight: bold;\n      }\n\n      .notSupported {\n        display: none;\n      }\n\n      .connected {\n        display: none;\n      }\n\n      .disconnected {\n        display: block;\n      }\n    }\n  }\n}",".tree-leaf {\n    font-weight: var(--label-font-weight);\n    font-size: var(--label-font-size);\n    display: flex;\n    flex-direction: column;\n    align-items: stretch;\n    float: unset;\n    min-height:28px;\n\n    .tree-leaf-content{\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      cursor: pointer;\n      transition: all 0.4s;\n      padding-left:3px;\n      height: 25px;\n      border-radius: @borderRadiusSmall;\n      &.tree-child-leaves{\n        transition: all 0.4s;\n      }\n      &:hover{\n        transition: all 0.4s;\n      }\n      &.selected{\n        transition: all 0.4s;\n      }\n      .tree-leaf-text{\n        float:unset;\n      }\n      .tree-expando {\n        float: unset;\n        top: unset;\n        position: unset;\n        width: 16px;\n        height: 16px;\n        line-height: 14px;\n        border-radius: @borderRadiusSmall;\n        border-width: 1px;\n        &.hidden{\n          display: block !important;\n          visibility: hidden !important;\n        }\n      }\n    }\n}\n",".modal-backdrop.in{\n  transition: all .4s linear;\n}\n\n.genericDialog {\n  .modal-content{\n    border-radius:@borderRadiusBig;\n    padding: @contentToEdgePadding;\n    display: flex;\n    flex-direction: column;\n    gap: @contentAreaSpacing;\n\n    .modal-header{\n      border-bottom:0;\n      font-weight:400;\n      box-shadow:none;\n      margin:0px;\n      padding: 0px;\n      h4{\n        padding-bottom:4px;\n      }\n    }\n\n    .modal-footer {\n      margin:0px;\n      padding: 0px;\n      border: none;\n      display: flex;\n      flex-direction: row;\n      justify-content: flex-end;\n      flex-wrap: nowrap;\n      gap: @buttonSpacing;\n      margin-top: @contentAreaSpacing;\n    }\n\n    .modal-body {\n      margin:0px;\n      padding: 0px;\n      min-height:120px;\n      max-height:80%;\n      overflow:auto;\n\n      .media{\n        padding:0px;\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        .media-left{\n          img{\n            width: 120px;\n            height: 120px;\n            object-fit: contain;\n          }\n        }\n        .media-body{\n          flex:1;\n          display: flex;\n          flex-direction: column;\n          // in the media body section wen have currently just inpt controls. Use the corect spacing\n          // between the different controll elements.\n          gap:@controlGroupSpacing;\n        }\n      }\n      .section {\n        border: 0px solid transparent !important;  \n        cursor: default !important;\n      }\n\n      .list-group {\n        overflow-y: auto;\n        overflow-x: auto;\n\n        *[data-draw2d=\"true\"] {\n          font-weight: bold;\n        }\n\n        .list-group-item {\n          background-color: transparent;\n          font-weight: 300;\n\n          &:hover {\n            text-decoration: underline;\n          }\n        }\n\n        *[data-draw2d=\"false\"][data-type=\"file\"] {\n          cursor: default;\n          text-decoration: none !important;\n\n        }\n      }\n    }\n  }\n}\n",".electra-button{\n    height: @buttonHeight;\n    line-height: @buttonHeight;\n    min-width: @buttonHeight * 2;\n    text-align: center;\n    outline-width: 0;\n    outline-style: none;\n    cursor:pointer;\n    border-radius: @borderRadiusSmall;\n    display: inline-block;\n    font-weight: 400;\n    padding-left:10px;\n    padding-right:10px;\n    border: none;\n    font-size:1em;\n}\n\n\ninput[type=text]{\n    height: 34px;\n    padding-left: 9px;\n    padding-right: 9px;\n    width:100%;\n    outline: none;\n    border-radius: @borderRadiusSmall;\n    box-shadow: none;\n    &:focus{\n        box-shadow: none;\n    }\n}\n\n.controlWithHeader{\n    display: flex;\n    flex-direction: column;\n    gap: @controlHeadingSpacing;\n    label{\n        margin:0px;\n    }\n\n}\n\n.inputWithButton{\n    display: flex;\n    flex-direction: row;\n    input{\n        flex:1 1px;\n    }\n}",".context-menu-list {\n  margin: 0;\n  padding: 0;\n\n  min-width: 120px;\n  max-width: unset;\n  display: inline-block;\n  position: absolute;\n  list-style-type: none;\n\n  border-width: 1px;\n  border-style: solid;\n  border-left-width: 2px;\n  font-size: 15px;\n  white-space: nowrap;\n\n  .context-menu-item {\n    padding: 5px 5px 5px 24px;\n    position: relative;\n    user-select: none;\n\n    &.hover {\n      cursor: pointer;\n    }\n\n    >label {\n\n      >input,\n      >textarea {\n        user-select: text;\n      }\n    }\n  }\n\n  .context-menu-separator {\n    padding-bottom: 0;\n    border-bottom-width: 1px;\n    border-bottom-style: solid;\n  }\n\n  .context-menu-input.hover,\n  .context-menu-item.disabled.hover {\n    cursor: default;\n  }\n\n  .context-menu-submenu:after {\n    content: \">\";\n    position: absolute;\n    top: 0;\n    right: 3px;\n    z-index: 1;\n  }\n\n  .context-menu-item.icon {\n    min-height: 18px;\n  }\n\n  .context-menu-item.icon:before {\n    position: relative;\n    left: -15px;\n    font-size: 19px;\n  }\n\n  /* vertically align inside labels */\n  .context-menu-input>label>* {\n    vertical-align: top;\n  }\n\n  /* position checkboxes and radios as icons */\n  .context-menu-input>label>input[type=\"checkbox\"],\n  .context-menu-input>label>input[type=\"radio\"] {\n    margin-left: -17px;\n  }\n\n  .context-menu-input>label>span {\n    margin-left: 5px;\n  }\n\n  .context-menu-input>label,\n  .context-menu-input>label>input[type=\"text\"],\n  .context-menu-input>label>textarea,\n  .context-menu-input>label>select {\n    display: block;\n    width: 100%;\n    box-sizing: border-box;\n  }\n\n  .context-menu-input>label>textarea {\n    height: 100px;\n  }\n\n  .context-menu-item>.context-menu-list {\n    display: none;\n    /* re-positioned by js */\n    right: -5px;\n    top: 5px;\n  }\n\n  .context-menu-item.hover>.context-menu-list {\n    display: block;\n  }\n\n  .context-menu-accesskey {\n    text-decoration: underline;\n  }\n\n  .context-menu-list {\n    border-color: rgb(var(--border-color));\n    background: white;\n    border-left-color: rgb(var(--tint-color));\n    box-shadow: var(--box-shadow-2);\n\n    .context-menu-separator {\n      border-bottom: 1px solid rgb(var(--border-color));\n    }\n\n    .context-menu-item {\n      &.hover {\n        background-color:rgb(var(--tint-color));\n        color: white;\n      }\n\n      &.disabled {\n        color: #666;\n\n        &.hover {\n          background-color: #EEE;\n        }\n      }\n\n      .context-menu-submenu:after {\n        color: #666;\n      }\n    }\n  }\n}","\n#fileOpenDialog {\n\n  .list-group{\n    height:60%;\n  }\n}\n","\n#githubFileSaveAsDialog {\n\n  .filePreview{\n    max-width: 200px;\n    max-height: 200px;\n  }\n\n\n  .list-group{\n    height:250px;\n  }\n}\n","#canvas_zoom{\n  position: fixed;\n  bottom: 20px;\n  right: @rightMenuWidth + 20px;\n  border-radius:@borderRadiusSmall;\n  display: flex;\n  flex-wrap: nowrap;\n  align-items: center;\n\n  button {\n    background-color:transparent;\n    font-weight:var(--label-font-weight);\n    font-size: var(--label-font-size);\n    border-radius:@borderRadiusSmall;\n    padding:5px;\n    padding-left:10px;\n    padding-right:10px;\n    border-width:1px;\n    border-style: solid;\n    outline:none;\n    transition: all 0.5s;\n    &:hover {\n      border-width:1px;\n      border-style: solid;\n    }\n  }\n}\n",".markdownRendering{\n\n  img {\n    max-width: 100%;\n  }\n\n  p{\n    font-size: 16px;\n    margin-top: 30px;\n  }\n  table {\n    /* we need important to override some setting in the wysiwyg editor **/\n    margin-left:auto !important;\n    margin-right:auto !important;\n    font-family:Arial, Helvetica, sans-serif;\n    font-size:12px;\n    border-width:1px;\n    border-style: solid;\n    border-radius:@borderRadiusSmall;\n  }\n  table th {\n    padding:10px;\n    border-width:1px;\n    border-style: solid;\n  }\n\n  table th:first-child{\n    text-align: left;\n    padding-left:20px;\n  }\n  table tr:first-child th:first-child{\n    border-top-left-radius:3px;\n  }\n  table tr:first-child th:last-child{\n    border-top-right-radius:3px;\n  }\n  table tr{\n    text-align: center;\n    padding-left:20px;\n  }\n  table tr td:first-child{\n    text-align: left;\n    padding-left:20px;\n    border-left: 0;\n  }\n  table tr td {\n    padding:18px;\n    border-width:1px;\n    border-style: solid;\n  }\n\n  table tr:last-child td{\n    border-bottom:0;\n  }\n\n  table tr:last-child td:first-child{\n    border-bottom-left-radius:3px;\n  }\n\n  table tr:last-child td:last-child{\n    border-bottom-right-radius:3px;\n  }\n\n  .info{\n    border-width:1px;\n    border-style: solid;\n    border-radius: @borderRadiusBig;\n    font-weight: 400;\n    letter-spacing: 2px;\n    padding: 5px;\n    padding: 5px;\n    padding-left: 20px;\n    padding-right: 20px;\n    p{\n      padding: 0;\n      margin:0;\n    }\n  }\n}\n","\n.tinyFlyove